<template>
	<view>
		<view  class="cu-bar bg-transparent search">
			<view  class="cu-avatar round ">
				<image src="../../static/images/OIP-C.jpg" mode="widthFix" class="round"></image>
			</view>
			<view class="text-left padding">
				白日梦想家
			</view>
			<view class="action">
				<view class="padding-xs">
					<view class='cu-tag round light bg-red'>关注</view>
				</view>
			</view>
		</view>
		<luyj-grid-link :list="list" ></luyj-grid-link>
		<view class="tower-swiper" @touchmove="TowerMove" @touchstart="TowerStart" @touchend="TowerEnd">
			<view class="tower-item" :class="item.zIndex==1?'none':''" v-for="(item,index) in swiperList" :key="index"
				:style="[{'--index': item.zIndex,'--left':item.mLeft}]" :data-direction="direction">
				<view class="swiper-item">
					<image :src="item.url" mode="aspectFill" v-if="item.type=='image'"></image>

				</view>
			</view>
		</view>
		<view style="padding: 40rpx;">
			<!-- 标题 -->
			<h2>探索天文馆~</h2>
			<!-- 主体内容 -->
			<view style="padding-top: 20rpx;line-height: 1.5;">天文馆是指以传播天文知识为主的科学普及机构。天象仪是其必需的设备，安置在半球形屋顶的天象厅中，可将各种天象投放在人造天幕上进行天象表演，并配合解说词说明各种天文现象。许多天文馆往往还建有从事天文普及活动的小型天文台。天文馆通过天象表演、天文讲座、放映天文科学教育电影、举办天文图片展览、出版天文普及书籍和刊物、组织天文观测活动、辅导制作小望远镜和天文教具等形式从事天文普及工作。
1928年德国慕尼黑市建立了世界上第一座天文馆，世界上已建有100多所大型天文馆 ，中小型天文馆更是不可胜数。中国于1958年建立了第一座较大型的天文馆——北京天文馆，此外中国各地还建有数十座较小型的天文馆。天文馆除开展普及天文知识、进行天文学宣传教育外，还进行天文观测并从事一定的天文学研究工作。天文馆越来越发展成为综合性的科学普及机构。现建立的天文馆，都开展天文学和相关学科的普及宣传活动。
</view>
			<!-- 话题 -->
			<view class="" style="color: blue;margin-bottom: 20rpx;margin-top: 20rpx;">#天文馆</view>
			<!-- 时间 地点 -->
			<view class="flex justify-between">
				<view style="font-size: 25rpx;color: darkgrey;">2022-11-15 23:23</view>
				<view class="iconfont icon-weizhi" style="line-height: 1.2;font-size: 25rpx;">上海天文馆</view>
			</view>
			<!-- 点赞 收藏帖子 收藏地点 -->
			<view class="flex justify-between" style="margin-top: 20rpx;">
				<view class="action">
					<view class="padding-xs">
						<view class='cu-tag round light bg-purple'>
							<view class="iconfont icon-dianzan2" style="font-size: 25rpx;padding: 10rpx;">21</view>
						</view>
					</view>
				</view>
				<view class="action">
					<view class="padding-xs">
						<view class='cu-tag round light bg-purple'>
							<view class="iconfont icon-shoucang" style="font-size: 25rpx;padding: 10rpx;">帖子</view>
						</view>
					</view>
				</view>
				<view class="action">
					<view class="padding-xs">
						<view class='cu-tag round light bg-purple'>
							<view class="iconfont icon-shoucang" style="padding: 10rpx;font-size: 25rpx;">地点</view>
						</view>
					</view>
				</view>
			</view>
		</view> 
		<!-- 占位 -->
		<view class="" style="height: 100rpx;"></view>
	</view>
</template>
<script>
	import data from '@/common/data/data.js'; // 数据
	export default {
		data() {
			return {
				TabCur: 0,
				scrollLeft: 0,
				list: data.current_list,
				cardCur: 0,
				swiperList: [{
					id: 0,
					type: 'image',
					url: '/static/images/OIP-C.jpg'
				}, {
					id: 1,
					type: 'image',
					url: '/static/images/OIP-C.jpg',
				}, {
					id: 2,
					type: 'image',
					url: '/static/images/OIP-C.jpg'
				}, {
					id: 3,
					type: 'image',
					url: '/static/images/OIP-C.jpg'
				}, {
					id: 4,
					type: 'image',
					url: '/static/images/OIP-C.jpg'
				}, {
					id: 5,
					type: 'image',
					url: '/static/images/OIP-C.jpg'
				}, {
					id: 6,
					type: 'image',
					url: '/static/images/OIP-C.jpg'
				}],
				dotStyle: false,
				towerStart: 0,
				direction: ''
			}
		},
		onLoad() {
            this.TowerSwiper('swiperList');
			// 初始化towerSwiper 传已有的数组名即可
		},
		methods: {
			tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
			},
			DotStyle(e) {
				this.dotStyle = e.detail.value
			},
			// cardSwiper
			cardSwiper(e) {
				this.cardCur = e.detail.current
			},
			// towerSwiper
			// 初始化towerSwiper
			TowerSwiper(name) {
				let list = this[name];
				for (let i = 0; i < list.length; i++) {
					list[i].zIndex = parseInt(list.length / 2) + 1 - Math.abs(i - parseInt(list.length / 2))
					list[i].mLeft = i - parseInt(list.length / 2)
				}
				this.swiperList = list
			},
		
			// towerSwiper触摸开始
			TowerStart(e) {
				this.towerStart = e.touches[0].pageX
			},
		
			// towerSwiper计算方向
			TowerMove(e) {
				this.direction = e.touches[0].pageX - this.towerStart > 0 ? 'right' : 'left'
			},
		
			// towerSwiper计算滚动
			TowerEnd(e) {
				let direction = this.direction;
				let list = this.swiperList;
				if (direction == 'right') {
					let mLeft = list[0].mLeft;
					let zIndex = list[0].zIndex;
					for (let i = 1; i < this.swiperList.length; i++) {
						this.swiperList[i - 1].mLeft = this.swiperList[i].mLeft
						this.swiperList[i - 1].zIndex = this.swiperList[i].zIndex
					}
					this.swiperList[list.length - 1].mLeft = mLeft;
					this.swiperList[list.length - 1].zIndex = zIndex;
				} else {
					let mLeft = list[list.length - 1].mLeft;
					let zIndex = list[list.length - 1].zIndex;
					for (let i = this.swiperList.length - 1; i > 0; i--) {
						this.swiperList[i].mLeft = this.swiperList[i - 1].mLeft
						this.swiperList[i].zIndex = this.swiperList[i - 1].zIndex
					}
					this.swiperList[0].mLeft = mLeft;
					this.swiperList[0].zIndex = zIndex;
				}
				this.direction = ""
				this.swiperList = this.swiperList
			},
		}
	}
</script>

<style>
	@import '@/colorui/main.css';
	@import '@/colorui/icon.css';
	.tower-swiper .tower-item {
		transform: scale(calc(0.5 + var(--index) / 10));
		margin-left: calc(var(--left) * 100upx - 150upx);
		z-index: var(--index);
	}
</style>
